import React from "react"
import Layout from './layout'
import AttorneyContent from './attorneyContent.js'
import { getPageTitle, getPageCanonical } from '../utils/attorneyPage.js'
import Footer from "./footer"


class AttorneyPageLayout extends React.Component {
  constructor(props) {
    super(props);
    this.getUpperContent = this.getUpperContent.bind(this);
  }

  getUpperContent() {
    return (
      <AttorneyContent
        firstName={this.props.firstName}
        lastName={this.props.lastName}
        position={this.props.position}
        admissions={this.props.admissions}
        educations={this.props.educations}
        attorneyQuote={this.props.attorneyQuote}
        bioProfile={this.props.bioProfile}
        attorneyPhotoSmall={this.props.attorneyPhotoSmall}
        attorneyPhotoDesktop={this.props.attorneyPhotoDesktop}
      />
    );
  }

  render() {
    return (<Layout
      title={getPageTitle(this.props.firstName, this.props.lastName)}
      canonical={getPageCanonical(this.props.slug)}
      upperContent={this.getUpperContent()}
      lowerContent={<Footer isShowFooterContent={false} />}
    />)
  }
}

export default AttorneyPageLayout
