import React from "react"
import Navbar from "./navbar"

class Paragraph extends React.Component {
  constructor(props) {
    super(props);
  }

  render() {
    return (
      <p className="paragraph" dangerouslySetInnerHTML={{__html:this.props.quote}}>
      </p>
    )
  }
}


class AttorneyQuote extends React.Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  render() {
    return (
      <div className="row__wrapper attorney__upper">
        <div className="attorney__frame attorney__frame--main">
          <img src={this.props.attorneyPhotoSmall.src} alt={this.props.name} className="lawyer__img lawyer__img--fullsize attorney__img" />
          <div className="attorney__img-panel">
            <img src={this.props.attorneyPhotoDesktop.src} alt={this.props.name} className="attorney__img-desktop" />

          </div>
          <div className="attorney__quote__panel">
            <div className="heading page__heading attorney__heading">
              <h1 className="heading__title uppercase attorney__heading-title">{this.props.quoterName}</h1>
              <p className="heading__desc attorney__desc">{this.props.position}</p>
            </div>
            <div className="attorney__quote">
              <Paragraph quote={this.props.children} />
            </div>
          </div>
        </div>
      </div>
    )
  }
}

class BioMenu extends React.Component {
  constructor(props) {
    super(props);

    this.getBioMenuClasses = this.getBioMenuClasses.bind(this);
    this.onMenuClicked = this.onMenuClicked.bind(this);
  }

  getBioMenuClasses() {
    return "bio__menu " + (this.props.currentActiveMenu === this.props.menuType ? "bio__menu--active" : "");
  }

  onMenuClicked() {
    this.props.onMenuClicked(this.props.menuType);
  }

  render() {
    return (
      <li className={this.getBioMenuClasses()} onClick={this.onMenuClicked} >{this.props.label}</li>
    )
  }
}

