class AttorneyBio extends React.Component {

  constructor(props) {
    super(props);

    this.state = {
      activeBioMenu: BIO_CONSTANT.BIO_PROFILE_MENU
    }

    this.isBioProfileActive = this.isBioProfileActive.bind(this);
    this.isBioEduActive = this.isBioEduActive.bind(this);
    this.isBioAdmActive = this.isBioAdmActive.bind(this);
    this.onBioMenuClicked = this.onBioMenuClicked.bind(this);
  }

  isBioProfileActive() {
    return this.state.activeBioMenu == BIO_CONSTANT.BIO_PROFILE_MENU;
  }

  isBioAdmActive() {
    return this.state.activeBioMenu == BIO_CONSTANT.BIO_ADM_MENU;
  }

  isBioEduActive() {
    return this.state.activeBioMenu == BIO_CONSTANT.BIO_EDU_MENU;
  }

  onBioMenuClicked(activatedMenu) {
    this.setState({ activeBioMenu: activatedMenu });
  }

  render() {
    return (
      <div className="page__row bio__row">
        <div className="row__wrapper attorney__lower">
          <div className="attorney__frame">
            <h1 className="attorney__heading">
              Meet
                <strong className="heading__title heading__title--condensed block attorney__bio-title">{this.props.name}</strong>
            </h1>
            <nav>
              <ul className="attorney__bio">
                {this.props.bioProfile ? <BioMenu label="Profile" onMenuClicked={this.onBioMenuClicked} currentActiveMenu={this.state.activeBioMenu} menuType={BIO_CONSTANT.BIO_PROFILE_MENU} /> : null}
                {this.props.bioEdu ? <BioMenu label="Education" onMenuClicked={this.onBioMenuClicked} currentActiveMenu={this.state.activeBioMenu} menuType={BIO_CONSTANT.BIO_EDU_MENU} /> : null}
                {this.props.bioAdm ? <BioMenu label="Admission" onMenuClicked={this.onBioMenuClicked} currentActiveMenu={this.state.activeBioMenu} menuType={BIO_CONSTANT.BIO_ADM_MENU} /> : null}
              </ul>
            </nav>
            <div className="bio__content">
              {this.props.bioProfile && this.isBioProfileActive() ? <BioProfile>{this.props.bioProfile}</BioProfile> : null}
              {this.props.bioEdu && this.isBioEduActive() ? this.props.bioEdu : null}
              {this.props.bioAdm && this.isBioAdmActive() ? this.props.bioAdm : null}
            </div>
          </div>
        </div>
      </div>
    )
  }
}

const QuoterName = ({ firstName, lastName }) => (
  <>
    {firstName} <em className="em">{lastName}</em>
  </>
);


class AttorneyContent extends React.Component {
  constructor(props) {
    super(props);
  }

  getBioAdm = (admissions) => {
    return (!this.props.admissions || this.props.admissions.length == 0) ? null : (<BioAdm admissions={admissions} />);
  }

  getBioEdu = (educations) => {
    return (!this.props.educations || this.props.educations.length == 0) ? null : (<BioEdu educations={educations} />);
  }

  getName() {
    return this.props.firstName + " " + this.props.lastName;
  }

  render() {
    return (
      <>
        <div className="content--fullbg attorney_bg">
          <Navbar></Navbar>
          <AttorneyQuote
            name={this.props.firstName + " " + this.props.lastName}
            quoterName={<QuoterName firstName={this.props.firstName} lastName={this.props.lastName} />}
            position={this.props.position}
            attorneyPhotoSmall={this.props.attorneyPhotoSmall}
            attorneyPhotoDesktop={this.props.attorneyPhotoDesktop}
          >
            {this.props.attorneyQuote}
          </AttorneyQuote>
        </div>
        <AttorneyBio name={this.getName()} bioProfile={this.props.bioProfile} bioEdu={this.getBioEdu(this.props.educations)} bioAdm={this.getBioAdm(this.props.admissions)} />
      </>
    )
  }
}

export default AttorneyContent;