import React from 'react';
import AttorneyPageLayout from '../components/attorneyPageLayout';
import { graphql } from 'gatsby';
const AttorneyPage = ({ data }) => {
    const attorneyData = data.attorneysYaml;
    return (
        <AttorneyPageLayout
            firstName={attorneyData.firstName}
            lastName={attorneyData.lastName}
            slug={attorneyData.slug}
            position={attorneyData.position}
            admissions={attorneyData.admissions}
            educations={attorneyData.educations}
            attorneyQuote={attorneyData.quote}
            bioProfile={attorneyData.bioProfile}
            attorneyPhotoSmall={attorneyData.smallPhoto.childImageSharp.fluid}
            attorneyPhotoDesktop={attorneyData.desktopPhoto.childImageSharp.fixed}
        />
    )
}

export default AttorneyPage;

export const query = graphql`
  query($slug: String!) {
    attorneysYaml(slug: {eq: $slug}) {
      admissions
      bioProfile
      educations
      firstName
      lastName
      position
      slug
      quote
      smallPhoto  {
        childImageSharp {
          fluid {
            ...GatsbyImageSharpFluid
          }
        }
      }
      desktopPhoto  {
        childImageSharp {
          fixed {
            ...GatsbyImageSharpFixed
          }
        }
      }
    }
  }
`